h1 {
  color: #0F0A28;
  font-family: Poppins;
  font-size: 20px;
  font-weight: 600;
}

p {
  color: #575368;
  font-family: "Open Sans";
  font-size: 12px;
}

a {
  color: #1496BE;
  font-family: "Open Sans";
  font-size: 12px;
  text-decoration: none;
}

.frame {
  width:900px; 
  height:100%; 
  margin:0px auto;
}

.top {
  width:100%;
  height: auto;
  padding-top: 10px;
  padding-bottom: 10px;
}

.top img {
  width: auto;
  height: 400px;
  margin-top: 20px;
}

.top p {
  color: #575368;
  font-family: "Open Sans";
  font-size: 16px;
}

.top a {
  font-size: 16px;
  font-weight: 600;
}

.top .left {
  width: 200px;
  height: 100%;
  float: left;
}

.top .right {
  width: auto;
  height: 100%;
  float: right;
}

.middle {
  width: 500px;
  height: 380px;
  margin:0px auto;
}

.middle .signup {
  width: 100%;
  height: auto;

  border-radius: 8px 8px 8px 8px;
  border-color: #B7B5BE;
  border-width: 1px;
  border-style: solid;
  text-align: center;
  float: center;
}

.middle .signup .title {
  margin-top: 20px;
  margin-bottom: 20px;
}

.middle .signup form {
  text-align: center;
}

.middle .signup form input {
  width: 272px;
  height: 40px;
  border-radius: 6px;
  background-color: #e7e6e9;
  border-width: 0px;
  margin-bottom: 8px;
  padding: 0 16px 0 16px;

  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
}

.middle .signup form input::placeholder {
  color: #868493;
}

.middle .signup form input:-ms-input-placeholder {
  color: #868493;
}

.middle .signup form input::-ms-input-placeholder {
  color: #868493;
}

.middle .signup form input:focus{
  outline: none;
}

.middle .signup form .submit{
  width: 304px;
  height: 38px;
  margin: auto;
  border-radius: 24px;
  background-color: #5ac3dc;
  margin-top: 8px;
  margin-bottom: 25px;
  display: flex;
  cursor: pointer;
}

.middle .signup form .submit p {
  margin: auto;
  text-align: center;
  color: #ffffff;    
  font-family: Poppins;
  font-size: 16px;
  font-weight: 600;
}

.middle .signup .errmsg {
  color: #ff0000;
  font-size: 16px;
  margin-bottom: 25px;
}

.middle .signin {
  width: 100%;
  height: 100px;
  margin-top: 8px;

  border-radius: 8px 8px 8px 8px;
  border-color: #B7B5BE;
  border-width: 1px;
  border-style: solid;
  text-align: center;
  
  line-height: 0px;
}

.middle .signin p {
  font-size: 16px;
}

.middle .signin a {
  font-size: 16px;
  font-weight: 400;
}

.bottom {
  width: 100%;
  height: 22px;

  margin-top: 0px;
  margin-bottom: 18px;
}

.bottom .left {
  width: 200px;
  height: 100%;
  float: left;
}

.bottom .left a {
  font-size: 16px;
  font-weight: 600;
}

.bottom .right {
  width: auto;
  height: 100%;
  float: right;
}

.bottom .right p {
  margin-top: 0px;
  height: 100%;
}

.center {
  float: center;
  text-align: center;
}

.vertical-center {
	justify-content: center;
	flex-direction: column;
	display: flex;
}

.vertical-direction {
  flex-direction: column;
	display: flex;
}

.horizontal-center {
  justify-content: center;
  flex-direction: row;
	display: flex;
}

.horizontal-direction {
  flex-direction: row;
	display: flex;
}


.padding-t-8 {
  padding-top: 8px;
}

.padding-b-8 {
	padding-bottom: 8px;
}

.padding-t-16 {
	padding-top: 16px;
}

.padding-b-16 {
	padding-bottom: 16px;
}

.padding-l-8 {
	padding-left: 8px;
}

.padding-l-16 {
	padding-left: 16px;
}

.margin-t-8 {
	margin-top: 8px
}

.margin-t-16 {
	margin-top: 16px;
}

.margin-b-8 {
	margin-bottom: 8px;
}

.margin-b-16 {
	margin-bottom: 16px;
}